const SEND_MESSAGE = 'SEND-MESSAGE';


let initialState = {
    dialogsData: [
        {
            name: 'Dima',
            id: '1',
            img: 'data:image/jpeg;base64,'
        },
        {
            name: 'Andreu',
            id: '2',
            img: 'data:image/jpeg;base64,'
        },
        {
            name: 'Sveta',
            id: '3',
            img: 'data:image/jpeg;base64,'
        },
        {
            name: 'Alena',
            id: '4',
            img: 'data:image/jpeg;base64,'
        },
        {
            name: 'Marina',
            id: '5',
            img: 'data:image/jpeg;base64,'
        }
    ],

    messageData: [
        {message: 'Hi', id: '1'},
        {message: 'Yo', id: '2'},
        {message: 'Where is my money', id: '3'},
        {message: 'Hate you', id: '4'},
        {message: 'Hate you', id: '5'}
    ],

};

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {

        case  SEND_MESSAGE:
            let body = action.newMessageBody;
            return {
                ...state,
                messageData: [...state.messageData, {message: body, id: '6'}]
            };

        default:
            return state;
    }
}

export const addMessage = (newMessageBody) => ({type: 'SEND-MESSAGE', newMessageBody})

export default dialogsReducer;